import httpService from './httpService'
const BASE_URL = 'api/exams'

export default {
    query,
    save,
    remove,
    get
}

async function query(userId) {
    return await httpService.get(`${BASE_URL}?userId=${userId}`);
}

async function get(id) {
    return await httpService.get(`${BASE_URL}/${id}`)
}


async function remove(id) {
    return await httpService.delete(`${BASE_URL}/${id}`)
}

async function save(exam) {
    var prm;
    if (exam._id) {
        prm = await httpService.put(`${BASE_URL}/${exam._id}`, exam)
    } else {
        prm = await httpService.post(`${BASE_URL}`, exam)
    }
    return prm
}

