import React from 'react'
import { Link } from 'react-router-dom'

export default function Option(props) {
    return (
        <Link to={props.className === 'exam' ? props.className + '/car' : props.className}>
            <article className={`option flex align-center ${props.className}`}>
                <h2 className="title">{props.title}</h2>
            </article>
        </Link>
    )
}
