import React from 'react'
import LastExamsPreview from './LastExamsPreview'
import { Link } from 'react-router-dom'

export default function LastExamsList(props) {
    if (!props.exams.length) return (
        <div className="never-container">
            <h2 className="never-exams-title">לא נבחנת לעולם! <Link to='/exam/car'>לחץ כאן למבחן הראשון שלך</Link></h2>
            <img src="../../assets/img/crying.png" className="never-img" alt="" />
        </div>
    )
    return (
        <div className="exams-list">
            {props.exams.map(exam => <LastExamsPreview exam={exam} key={exam._id} />)}
        </div>
    )
}
