import React from 'react'

export default function AnswerPreview(props) {

    const isSelected = (props.question.selectedAns === props.answer) ? true : false
    const { isExamFinish } = props

    function getIsCorrectAnswer() {
        return props.question.correctAnswer === props.answer
    }

    return (
        <div className="answer flex align-center" onClick={() => props.onSetSelectedAns(props.answer)}>
            {!isExamFinish && <span className={`square ${isSelected ? 'selected' : ''}`}></span>}

            {isExamFinish && getIsCorrectAnswer() && <span className='square selected'></span>}
            {isExamFinish && !getIsCorrectAnswer() && props.question.selectedAns === props.answer && <span className='square wrong'></span>}
            {isExamFinish && !getIsCorrectAnswer() && props.question.selectedAns !== props.answer && <span className='square'></span>}
            {props.answer}
        </div>
    )
}
