import React, { useState, useEffect } from 'react'
import { NavLink, Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'
import { logout } from '../store/actions/userActions'

export default function NavBar() {
    const loggedInUser = useSelector(state => state.user.loggedInUser)
    const dispatch = useDispatch()

    const [isScroll, setScroll] = useState(document.documentElement.scrollTop > 0)
    useEffect(() => {
        window.addEventListener('scroll', onScroll)
        return () => {
            window.removeEventListener('scroll', onScroll)
        }
    })
    function onScroll() {
        if (document.documentElement.scrollTop > 0) setScroll(true)
        else setScroll(false)
    }

    const [isMenuOpen, setMenuOpen] = useState(false)

    function getIsExam() {
        return window.location.pathname.includes('exam')
    }

    function onLogout() {
        dispatch(logout())
    }

    function onToggleMenu() {
        setMenuOpen(!isMenuOpen)
    }

    return (
        <nav className={`nav-container ${isScroll ? 'scrolled' : ''}`}>
            <div className="container flex align-center space-between">
                <ul className={`main-nav clean-list flex ${isMenuOpen ? 'menu-open' : ''}`}>
                    <div className="greet-close-container flex align-center even">
                        {loggedInUser && <li className="mobile-greet">היי {loggedInUser.username} - <small onClick={onLogout}>(התנתק)</small></li>}
                        <button onClick={onToggleMenu} className="close-menu"></button>
                    </div>
                    <li onClick={onToggleMenu}><NavLink to="/" exact>עמוד הבית</NavLink></li>
                    {!getIsExam() && <li onClick={onToggleMenu}><NavLink to="/exam/all">מבחן תיאוריה</NavLink></li>}
                    {!loggedInUser && <li onClick={onToggleMenu}><NavLink to="/login">הרשמה / התחברות</NavLink></li>}
                    <li onClick={onToggleMenu}><NavLink to="/contact">צור קשר</NavLink></li>
                </ul>
                <div className="screen" onClick={onToggleMenu}></div>
                <button className="menu-btn" onClick={onToggleMenu}></button>
                {loggedInUser && <span className="greet flex align-center">
                    היי {loggedInUser.username}, מה עם איזה <Link to='/exam/car'>מבחן</Link>?
                    <small onClick={onLogout} className="logout">התנתק</small>
                </span>}
                <div className="logo-container" dir="ltr">
                    <Link to="/" className="flex align-center">
                        <img src="../../assets/img/logo.svg" className="logo-icon" alt="" />
                        <h1 className="logo">goTheory</h1>
                    </Link>
                </div>
            </div>
        </nav>
    )
}
