import React, { useState } from 'react'
import { Link } from 'react-router-dom'

export default function FinishExam(props) {
    const [isWatchAfterFinish, setIsWatchAfterFinish] = useState(false)

    function onBackToExam() {
        setIsWatchAfterFinish(true)
    }

    const { exam } = props
    if (isWatchAfterFinish) return '';
    return (
        <>
            <div className={`screen ${isWatchAfterFinish ? 'no-display' : ''}`}></div>
            <section className={`modal-container ${isWatchAfterFinish ? 'no-display' : ''}`}>
                <header className="flex align-center">
                    <h2 className="title">תוצאות המבחן</h2>
                </header>
                <div className="content">
                    <div className="wrong-counter">
                        <h3 className="wroung-counter">מספר הטעויות: <span className="bold">{exam.wrongCounter}</span></h3>
                        <h4 className="pass">
                            {exam.wrongCounter > 4 && 'לא עברת את המבחן...'}
                            {exam.wrongCounter <= 4 && 'מזל טוב! עברת את המבחן!'}
                        </h4>
                    </div>
                </div>
                <footer>
                    <Link to={`/exam/${props.currExamType}`} className="btn primary">מבחן חדש</Link>
                    <button onClick={onBackToExam} className="btn danger">מה הטעויות שלי?</button>
                </footer>
            </section>
        </>
    )
}
