import React, { useState } from 'react'
import emailService from '../../services/emailService'

export default function NewsLetter() {
    const [email, setEmail] = useState('')
    const [msg, setMsg] = useState('')

    function handleChange(ev) {
        setEmail(ev.target.value)
    }

    async function onAddEmail(ev) {
        ev.preventDefault()
        if (!email) return setMsg('יש להזין כתובת מייל')
        try {
            await emailService.add({ email })
            setMsg('תודה שהצטרפת לעדכונים שלנו!')
            setEmail('')
        } catch (err) {
            console.log('Can\'t add email', err)
        }
    }

    return (
        <>
            <section className="news-letter-container flex align-center even">
                <h2 className="title">
                    הצטרפו לעדכונים חמים עכשיו!
                    {msg && <div className="msg">{msg}</div>}
                </h2>
                <form className="news-letter-form flex align-start" onSubmit={onAddEmail}>
                    <input type="email" className="input" autoComplete="off" value={email} onChange={handleChange} name="email" placeholder="כתובת המייל שלך" />
                    <button className="btn">עדכן אותי!</button>
                </form>
            </section>
        </>
    )
}
