import React from 'react'
import Option from './Option'
import { useSelector } from 'react-redux'

export default function OptionList() {
    const loggedInUser = useSelector(state => state.user.loggedInUser)
    return (
        <section className="options-container container flex justify-center wrap">
            {!loggedInUser && <Option title="התחבר לאתר לקבלת אופציות נוספות" className="login" />}
            <Option title="מבחן תיאוריה" className="exam" />
            {loggedInUser && <Option title="מבחנים אחרונים" className="last-exams" />}
            <Option title="צור איתנו קשר" className="contact" />
        </section>
    )
}
