import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import examService from '../services/examService'
import { useHistory } from 'react-router-dom'
import LastExamsList from '../cmps/LastExamsList'
import Loader from '../cmps/Loader'

export default function LastExamsPage() {
    const history = useHistory()
    const loggedInUser = useSelector(state => state.user.loggedInUser)
    const [exams, setExams] = useState(null)

    useEffect(() => {
        if (!loggedInUser) return history.push('/')
        async function loadExams() {
            setExams(await examService.query(loggedInUser._id))
        }
        loadExams()
    }, [])

    return (!exams ? <Loader /> :
        <main className="main-content page">
            <section className="last-exams-container container">
                <h2 className="page-title">מבחנים אחרונים</h2>
                <div className="content">
                    <LastExamsList exams={exams} />
                </div>
            </section>
        </main>
    )
}
