import React, { useState, useEffect } from 'react'
import { useLocation, Link } from 'react-router-dom'
import { useLoginForm } from '../hooks/loginForm'
import { login, signup } from '../store/actions/userActions'
import { useDispatch, useSelector } from 'react-redux'

export default function LoginSignupPage(props) {
    const disaptch = useDispatch()
    let currLocation = useLocation();
    const currPage = currLocation.pathname === '/login' ? 'login' : 'signup'

    const loggedInUser = useSelector(state => state.user.loggedInUser)
    useEffect(() => {
        if (loggedInUser) return props.history.push('/')
    }, [loggedInUser])

    const { inputs, handleInputChange, handleSubmit } = useLoginForm(onSubmit);

    const [msg, setMsg] = useState('')
    useEffect(() => {
        const msgTimeout = setTimeout(() => {
            setMsg('')
        }, 4000);
        return () => {
            clearTimeout(msgTimeout)
        }
    }, [msg])

    async function onSubmit() {
        const userCred = { username: inputs.username, password: inputs.password, fullname: inputs.fullname }
        if (!userCred.username || !userCred.password) return setMsg('נא למלא את שם המשתמש והסיסמא!')

        try {
            if (currPage === 'login') disaptch(login(userCred))
            else disaptch(signup(userCred))
        } catch (err) {
            return setMsg(err)
        }
    }

    return (
        <main className="main-content page">
            <section className="login-signup-container container">
                <h2 className="page-title">{currPage === 'login' ? 'התחברות לאתר' : 'הרשמה לאתר'}</h2>
                {currPage === 'login' && <Link to="/signup" className="signup-now">עדיין לא נרשמת? הירשם עכשיו!</Link>}
                {currPage === 'signup' && <Link to="/login" className="signup-now">רשום לאתר? התחבר עכשיו!</Link>}
                <form className="login-signup-form flex column align-start" onSubmit={handleSubmit}>
                    <input type="text" name="username" className="input" placeholder="שם משתמש" value={inputs.username ?? ''} onChange={handleInputChange} autoComplete="off" />
                    <input type="password" name="password" className="input" placeholder="סיסמא" value={inputs.password ?? ''} onChange={handleInputChange} autoComplete="off" />
                    {currPage === 'signup' && <input type="name" className="input" name="fullname" placeholder="שם מלא" value={inputs.fullname ?? ''} onChange={handleInputChange} autoComplete="off" />}
                    <button className="btn">{currPage === 'login' ? 'התחבר לאתר' : 'הירשם לאתר'}</button>
                </form>
                {msg && <div className="msg">{msg}</div>}
            </section>
        </main>
    )
}