import React from 'react'
import AnswerPreview from './AnswerPreview'

export default function Question(props) {
    const { onSetSelectedAns, exam } = props
    return (
        <section className="question-container">
            <h2 className="question">{exam.currQuestion.title}</h2>
            <div className="question-content flex align-center">
                <div className="answers-container">
                    {exam.currQuestion.answers.map((ans, idx) => <AnswerPreview key={idx} question={exam.currQuestion} isExamFinish={exam.isFinish} answer={ans} onSetSelectedAns={onSetSelectedAns} />)}
                </div>
                {exam.currQuestion.img && <div className="img-container">
                    <img src={exam.currQuestion.img} className="question-img" alt="" />
                </div>}
            </div>
        </section>
    )
}
