import React from 'react'
import moment from 'moment'
import 'moment/locale/he';
import { Link } from 'react-router-dom'

export default function LastExamsPreview(props) {

    const { exam } = props

    function getType(type) {
        switch (type) {
            case 'car':
                return 'רכב פרטי'
            case 'bus':
                return 'אוטובוס'
            case 'truck':
                return 'משאית'
            case 'tractor':
                return 'טרקטור'
            case 'motorcycle':
                return 'אופנוע'
            default:
                return 'שאלות כלליות';
        }
    }

    return (
        <article className="exam flex align-center">
            <img src="../../assets/img/exam-icon.png" className="icon" alt="" />
            <span>מספר טעויות: {exam.wrongCounter}</span>
            <span className={`status ${exam.wrongCounter > 4 ? 'failed' : 'passed'}`}>סטטוס מבחן: {exam.wrongCounter > 4 ? 'נכשל' : 'עבר'}</span>
            <span className="finished-at">{moment(exam.finishedAt).fromNow()}</span>
            <span className="exam-type">סוג מבחן: {getType(exam.type)}</span>
            <span><Link to={`/exam/${exam.type}/${exam._id}`}>צפייה במבחן</Link></span>
        </article>
    )
}
