import React, { useState } from 'react'
import { Link } from 'react-router-dom'

export default function TestNow() {
    const [isCarHover, setCarHover] = useState(false)
    const [isMotorcycleHover, setMotorcycleHover] = useState(false)
    const [isTractorHover, setTractorHover] = useState(false)
    const [isTruckHover, setTruckHover] = useState(false)
    const [isBusHover, setBusHover] = useState(false)

    return (
        <>
            <section className="test-now-container">
                <div className="container flex align-center">
                    <h4 className="title flex align-center justify-center">בחן את עצמך עכשיו</h4>
                    <div className="test-options flex align-center">
                        <Link to="/exam/car" className="car option" onMouseOver={() => setCarHover(true)} onMouseLeave={() => setCarHover(false)}></Link>
                        <Link to="/exam/motorcycle" className="motorcycle option" onMouseOver={() => setMotorcycleHover(true)} onMouseLeave={() => setMotorcycleHover(false)}></Link>
                        <Link to="/exam/tractor" className="tractor option" onMouseOver={() => setTractorHover(true)} onMouseLeave={() => setTractorHover(false)}></Link>
                        <Link to="/exam/truck" className="truck option" onMouseOver={() => setTruckHover(true)} onMouseLeave={() => setTruckHover(false)}></Link>
                        <Link to="/exam/bus" className="bus option" onMouseOver={() => setBusHover(true)} onMouseLeave={() => setBusHover(false)}></Link>
                    </div>
                </div>
            </section>
            <div className="test-txt-container flex">
                <span className={`car-txt txt ${isCarHover ? 'show-txt' : 'remove-txt'}`}>תיאוריה לרכב</span>
                <span className={`motorcycle-txt txt ${isMotorcycleHover ? 'show-txt' : 'remove-txt'}`}>תיאוריה לאופנוע</span>
                <span className={`tractor-txt txt ${isTractorHover ? 'show-txt' : 'remove-txt'}`}>תיאוריה לטרקטור</span>
                <span className={`truck-txt txt ${isTruckHover ? 'show-txt' : 'remove-txt'}`}>תיאוריה למשאית</span>
                <span className={`txt ${isBusHover ? 'show-txt' : 'remove-txt'}`}>תיאוריה לאוטובוס</span>
            </div>
        </>
    )
}
