import React, { useState } from 'react'
import { useLoginForm } from '../hooks/loginForm'
import msgService from '../services/msgService'
import { useEffect } from 'react';

export default function ContactPage() {

    const { inputs, handleInputChange, handleSubmit } = useLoginForm(onSubmit);
    const [msg, setMsg] = useState('')
    useEffect(() => {
        const msgTimeout = setTimeout(() => {
            setMsg('')
        }, 5000);
        return () => {
            clearTimeout(msgTimeout)
        }
    }, [msg])

    async function onSubmit() {
        const msg = { fullname: inputs.fullname, email: inputs.email, phone: inputs.phone, content: inputs.content }
        if (!msg.fullname || !msg.email || !msg.phone || !msg.content) return setMsg('כל השדות הם חובה, נא למלא את כולם!')
        try {
            await msgService.add(msg)
            setMsg('הודעתך נשלחה בהצלחה ותענה בהקדם, תודה!')
        } catch (err) {
            console.log('ERROR:', err)
            setMsg(err)
        }
    }

    return (
        <main className="main-content page">
            <section className="contact-container container">
                <h2 className="page-title">יצירת קשר</h2>
                <div className={`msg ${msg ? 'show' : ''}`}>{msg}</div>
                <div className="flex">
                    <form className="contact-form flex column align-start" onSubmit={handleSubmit}>
                        <input type="text" name="fullname" placeholder="שם מלא" autoComplete="off" onChange={handleInputChange} />
                        <input type="email" name="email" placeholder="אימייל" autoComplete="off" onChange={handleInputChange} />
                        <input type="phone" name="phone" placeholder="טלפון" autoComplete="off" onChange={handleInputChange} />
                        <textarea name="content" placeholder="תוכן ההודעה" onChange={handleInputChange}></textarea>
                        <button className="btn">שלח הודעה</button>
                    </form>
                    <div className="contact-icon">@</div>
                </div>
            </section>
        </main>
    )
}