import React from 'react'
import { Link } from 'react-router-dom'

export default function Hero() {
    return (
        <div className="hero-container container flex space-between">
            <div className="cta-container flex column align-start">
                <h2 className="header-main-title">לימוד למבחן תיאוריה בזמן קצר</h2>
                <Link to="/exam/car" className="cta-btn">לא יכול לחכות, למד אותי!</Link>
            </div>
            <div className="img-container">
                <img src="assets/img/header-car.png" alt="" />
            </div>
        </div>
    )
}
