import React, { useCallback } from 'react'
import { Switch, Route, useLocation } from 'react-router-dom'
import './style/global.scss';
import NavBar from './cmps/NavBar'
import Footer from './cmps/Footer'
import HomePage from './pages/HomePage'
import ExamPage from './pages/ExamPage'
import LoginSignupPage from './pages/LoginSignupPage'
import LastExamsPage from './pages/LastExamsPage'
import ContactPage from './pages/ContactPage'

export default function App() {
  const location = useLocation()
  let page;
  const setCurrPage = useCallback(() => {
    page = location.pathname
    window.scrollTo(0, 0)
  }, [location]);
  setCurrPage()

  return (
    <>
      <NavBar />
      <Switch>
        <Route component={ExamPage} path="/exam/:type/:id?" />
        <Route component={ContactPage} path="/contact" />
        <Route component={LastExamsPage} path="/last-exams" />
        <Route component={LoginSignupPage} path="/signup" />
        <Route component={LoginSignupPage} path="/login" />
        <Route component={HomePage} path="/" />
      </Switch>
      <Footer />
    </>
  );
}