import React from 'react'
import { Link } from 'react-router-dom'
import FooterContent from './FooterContent'

export default function Footer() {
    return (
        <footer className="main-footer">
            <div className="footer-container container flex space-between align-center">
                <div className="logo-container footer-logo-container" dir="ltr">
                    <Link to="/" className="flex align-center">
                        <img src="../../assets/img/logo.svg" className="logo-icon" alt="" />
                        <h1 className="logo">goTheory</h1>
                    </Link>
                </div>
                <div className="content-container flex">
                    <FooterContent title="אודות goTheory">
                        <p>
                            אתר goTheory נבנה ע"י <a href="https://www.linkedin.com/in/shai-sharakanski-7812b11a6/" rel="noopener noreferrer" target="_blank">שי שרקנסקי</a>, ללא כל כוונות רווח.
                            כל מטרת האתר היא לימוד והצגת יכולות תכנות.
                            מוזמנים להנות וללמוד בעזרת האתר, שתפו לחברים שצריכים לעשות תאוריה.
                        </p>
                    </FooterContent>
                    <FooterContent title="מבחן תאוריה">
                        <ul className="clean-list">
                            <li><Link to="/exam/car">תאוריה לרכב</Link></li>
                            <li><Link to="/exam/motorcycle">תאוריה לאופנוע</Link></li>
                            <li><Link to="/exam/tractor">תאוריה לטרקטור</Link></li>
                            <li><Link to="/exam/truck">תאוריה למשאית</Link></li>
                            <li><Link to="/exam/bus">תאוריה לאוטובוס</Link></li>
                        </ul>
                    </FooterContent>
                    <FooterContent title="ניווט באתר">
                        <ul className="clean-list">
                            <li><Link to="/">עמוד הבית</Link></li>
                            <li><Link to="/exam/car">מבחן תאוריה</Link></li>
                            <li><Link to="/signup">הרשמה לאתר</Link></li>
                            <li><Link to="/login">התחברות לאתר</Link></li>
                            <li><Link to="/contact">צור קשר</Link></li>
                        </ul>
                    </FooterContent>
                </div>
            </div>
        </footer>
    )
}
