import React, { useState, useEffect, useRef } from 'react'
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux'

import { questions } from '../questionsNew'
import Question from '../cmps/Question'
import { getRandom } from '../services/utilService'
import FinishExam from '../cmps/FinishExam'
import examService from '../services/examService'
import Loader from '../cmps/Loader'

export default function ExamPage(props) {
    const examInterval = useRef()
    const history = useHistory()
    const loggedInUser = useSelector(state => state.user.loggedInUser)

    const [exam, setExam] = useState(null)
    useEffect(() => {
        const currType = props.match.params.type
        const currId = props.match.params.id
        if (!currId) {
            async function createCurrExam() {
                const newExam = await createExam()
                setExam(newExam)
                history.replace(`/exam/${currType}/${newExam._id}`)
            }
            createCurrExam()
        } else {
            async function loadExistExam() {
                const loadedExam = await loadExam(currId)
                setExam(loadedExam)
            }
            loadExistExam()
        }
    }, [props.match.params.type, props.match.params.id])

    const [timerToShow, setTimer] = useState('40:00')
    useEffect(() => {
        let timer = 40 * 60;
        const intervalId = setInterval(async () => {
            let minutes = parseInt(timer / 60, 10);
            let seconds = parseInt(timer % 60, 10);

            minutes = minutes < 10 ? "0" + minutes : minutes;
            seconds = seconds < 10 ? "0" + seconds : seconds;

            setTimer(minutes + ":" + seconds);

            if (--timer < 0 || exam?.isFinish) {
                timer = 60 * 40;
                const newExam = { ...exam }
                newExam.isFinish = true
                setExam(newExam)
                await examService.save(newExam)
                clearInterval(examInterval.current)
            }
        }, 1000);
        examInterval.current = intervalId
        return () => {
            clearInterval(examInterval.current)
        }
    }, [exam?.isFinish])

    async function createExam() {
        const thirtyQuestions = getRandomThirtyQuestions()
        const newExam = {
            questions: thirtyQuestions,
            currQuestion: thirtyQuestions[0],
            isFinish: false
        }
        return await examService.save(newExam)
    }

    async function loadExam(id) {
        return await examService.get(id)
    }

    const currExam = getExamHeName()
    function getExamHeName() {
        const enExamName = props.match.params.type
        switch (enExamName) {
            case 'car':
                return 'רכב פרטי'
            case 'bus':
                return 'אוטובוס'
            case 'truck':
                return 'משאית'
            case 'tractor':
                return 'טרקטור'
            case 'motorcycle':
                return 'אופנוע'
            default:
                return 'שאלות כלליות';
        }
    }

    function getSpecificQuestions() {
        const enExamName = props.match.params.type
        let char;
        switch (enExamName) {
            case 'car':
                char = 'B';
                break;
            case 'motorcycle':
                char = 'A'
                break;
            case 'tractor':
                char = '1'
                break;
            case 'truck':
                char = 'C'
                break;
            case 'bus':
                char = 'D'
                break;
            default:
                char = ''
                break;
        }
        const currQuestions = questions.filter(q => q.license.includes(char))
        return currQuestions
    }

    function getRandomThirtyQuestions() {
        const questions = getSpecificQuestions()
        let firstI = getRandom(0, questions.length - 1)
        return questions.slice(firstI, firstI + 30)
    }

    function onSetSelectedAns(answer) {
        if (exam.isFinish) return
        const selectedAnsIdx = exam.currQuestion.answers.findIndex(ans => ans === answer)
        const selectedAns = exam.currQuestion.answers[selectedAnsIdx]
        const newExam = { ...exam }
        const currQuestionIdx = getCurrQuestionIdx()
        newExam.questions[currQuestionIdx].selectedAns = selectedAns
        newExam.currQuestion.selectedAns = selectedAns
        setExam(newExam)
    }

    function getCurrQuestionIdx() {
        return exam.questions.findIndex(q => q.id === exam.currQuestion.id)
    }

    function onSetCurrQuestion(questionNumber) {
        const newExam = { ...exam }
        newExam.currQuestion = newExam.questions[questionNumber]
        setExam(newExam)
    }

    async function onFinishExam() {
        const newExam = { ...exam }

        const isAllQuestionsDone = newExam.questions.every(q => q.selectedAns)
        let isConfirmNotAllQuestions;
        if (!isAllQuestionsDone)
            isConfirmNotAllQuestions = window.confirm('לא ענית על כל השאלות, האם אתה בטוח שאתה רוצה לסיים את המבחן?')
        if (!isConfirmNotAllQuestions && !isAllQuestionsDone) return;

        const wrongCounter = newExam.questions.reduce((acc, q) => {
            if (q.selectedAns !== q.correctAnswer) acc++
            return acc
        }, 0)
        newExam.wrongCounter = wrongCounter

        newExam.questions.forEach(q => {
            if (q.selectedAns !== q.correctAnswer) q.isWrong = true
        })

        newExam.isFinish = true
        if (loggedInUser) {
            newExam.user = loggedInUser
        }
        newExam.finishedAt = new Date()
        newExam.type = props.match.params.type
        clearInterval(examInterval.current)
        setExam(newExam)
        await examService.save(newExam)
    }

    function getQuestionBg(question) {
        if (question.isWrong) return 'red-bg'
        else return 'green-bg'
    }

    return (!exam ? <Loader /> :
        <main className="main-content page">
            {exam.isFinish && <FinishExam exam={exam} currExamType={props.match.params.type} />}
            <section className="exam-container container">
                <h2 className="page-title">מבחן תאוריה {currExam}</h2>
                <div className="questions-timer-container flex even align-center">
                    <div className="questions-number-container flex wrap">
                        {exam.questions.map((q, i) => <div key={i} className={`question-number pointer ${exam.isFinish && getQuestionBg(q)} ${i === getCurrQuestionIdx() ? 'curr-question' : ''}`} onClick={() => onSetCurrQuestion(i)}>שאלה {i + 1}</div>)}
                    </div>
                    <div className="timer-container flex justify-center align-center">{timerToShow}</div>
                </div>

                <Question exam={exam} onSetSelectedAns={onSetSelectedAns} />

                <div className="navigate-btns flex space-between">
                    <button className="btn navigate-btn" onClick={() => onSetCurrQuestion(getCurrQuestionIdx() - 1)} disabled={getCurrQuestionIdx() === 0}>שאלה קודמת</button>
                    <button className="btn navigate-btn" onClick={() => onSetCurrQuestion(getCurrQuestionIdx() + 1)} disabled={getCurrQuestionIdx() === exam.questions.length - 1}>שאלה הבאה</button>
                </div>
                {!exam.isFinish && <button className="finish-exam-btn btn" onClick={onFinishExam}>סיים מבחן</button>}
            </section>
        </main>
    )
}